.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
"h h"
"n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 5fr 20fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background: LightSalmon;
}

.content img {
    width: 600px;
}
