.loginBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.form {
    background-color: moccasin;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    width: 350px;
    height: auto;
    border: 3px solid white;
    border-radius: 10px 10px 10px 10px;
}

.input {
    width: 230px;
    height: 20px;
}

.formSummaryError {
    border: solid red 5px;
    padding: 10px;
    color: crimson;
}